<template>
  <div class="login">
  	<div class="box" v-if="!isReg">
  		<h3>欢迎登录</h3>
  		<div class="input_box">
  			<div class="title">账号</div>
  			<input type="" name="" id="" value="" placeholder="请输入账号" v-model="name"/>
  		</div>
  		<div class="input_box">
  			<div class="title">密码</div>
  			<input type="password" name="" id="" value="" placeholder="请输入密码" v-model="pwd"/>
  		</div>
  		<div class="btns">
  			<div class="login-btn" @click="login()">登录</div>
  			<div class="reg-btn" @click="reg()">注册</div>
  		</div>
  	</div>
  	<div class="box" v-else>
  		<h3>欢迎注册</h3>
  		<div class="input_box">
  			<div class="title">账号</div>
  			<input type="" name="" id="" value="" placeholder="请输入账号" v-model="name"/>
  		</div>
  		<div class="input_box">
  			<div class="title">密码</div>
  			<input type="password" name="" id="" value="" placeholder="请输入密码" v-model="pwd"/>
  		</div>
  		<div class="input_box">
  			<div class="title">确认</div>
  			<input type="password" name="" id="" value="" placeholder="请确认密码" v-model="pwd2"/>
  		</div>
  		<div class="btns">
  			<div class="login-btn" @click="add_user()">确认</div>
  			<div class="reg-btn" @click="cancel()">取消</div>
  		</div>
  	</div>
  </div>
</template>

<script>
// @ is an alias to /src

export default {
  name: 'login',
  data(){
  	return{
  		isReg:false,
  		name:"",
  		pwd:"",
  		pwd2:""
  	}
  },
  methods:{
  	login(){
  		if(this.pwd==""||this.name==""){
  			alert('请输入完整信息');
  			return 0;
  		}
  		if(localStorage.getItem('name')==this.name&&localStorage.getItem("pwd")==this.pwd){
  			this.name = "";
  			this.pwd = "";
  			this.$router.push('/home/list');
  		}else{
  			alert('账号或密码不正确')
  		}
  	},
  	reg(){
  		this.isReg = true;
  		this.name = "";
  		this.pwd = "";
  	},
  	add_user(){
  		if(this.pwd==""||this.pwd2==""||this.name==""){
  			alert('信息未填写完整');
  			return 0;
  		}
  		if(this.pwd==this.pwd2){
  			localStorage.setItem('name',this.name);
  			localStorage.setItem('pwd',this.pwd);
  			this.name="";
  			this.pwd="";
  			this.isReg = false;
  		}else{
  			alert('两次密码不一致')
  		}
  	},
  	cancel(){
  		this.isReg = false;
  		this.name = "";
  		this.pwd = "";
  		this.pwd2 = "";
  	}
  }
};
</script>

