import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
		lists:[
			{
				title:"说明",
				content:'个人中心点击添加按钮即可发布新的文章'
			}
		],
		detail:{}
  },
  mutations: {
		addItem(state,val){
			state.lists.push(val)
		},
		look_detail(state,index){
			state.detail = state.lists[index];
		}
  },
  actions: {

  },
});
